body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    text-align: center;
  }
  
  .App-logo {
    height: 80px;
  }
  
  .App-header {
    background-color: #222;
    padding: 20px;
    color: white;
  }
  
  .App-header .title{
    margin-top: 40px;
  }
  
  .App-header a{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: white;
    -webkit-transition: .2s all;
    transition: .2s all;
  }
  .App-header a:hover{
    opacity: .7;
  }
  
  .githubIcon{
    height: 30px;
    margin-right: 5px;
  }
  
  .countUp{
    margin: 10px auto;
    padding-bottom: 20px;
  }
  
  .countUp-col{
    display: inline-block;
  }
  
  .countUp-col-element{
    display: inline-block;
    margin: 0 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  
  .countUp-col-element strong{
    font-size: 100px;
  }
  
